* {
  margin: 0;
}

body {
  background-color: aliceblue;
  color: black;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.header {
  text-align: center;
  padding: 2.5%;
  background-color: #003459;
  color: aliceblue;
  align-items: center;
  justify-content: center;
  display: flex;
}

.greeting {
  font-size: 4vw;
  font-weight: bolder;
  margin: 5%;
  text-align: left;
  line-height: 200%;
  font-family: Arial, Helvetica, sans-serif;
}

.greeting .name {
  background-color: #00171F;
  padding: 0.3em;
  border-radius: 0.5em;
}

.header .image {
  width: 25%;
  border-radius: 50%;
  border: 0.8vw solid #00171F;
}

.footer {
  position: absolute;
  left:0;
  bottom:0;
  right:0;
  text-align: center;
  padding: 2em;
  background-color: #003459;
  color: white;
  font-weight: bold;
  font-size: 1vw;
}